const InitUpdate = lazy(
  () =>
    new Promise(async (resolve) => {
      const module = await import("./InitUpdate");
      NUMBER_OF_LOADED_COMPONENTS.inc();
      resolve(module);
    })
);

const TopSignInAppBar = lazy(
  () =>
    new Promise(async (resolve) => {
      const module = await import("./General/Navigation/TopSignInAppBar");
      NUMBER_OF_LOADED_COMPONENTS.inc();
      resolve(module);
    })
);

const Main = lazy(
  () =>
    new Promise(async (resolve) => {
      const module = await import("./Main");
      NUMBER_OF_LOADED_COMPONENTS.inc();
      resolve(module);
    })
);

export default function Root() {
  return (
    <>
      <FireBaseAuthProviderX>
        <GoogleApiAuthProviderX>
          <LocalForageProvider>
            <TopTranformerTriggerProvider>
              <InitUpdate />

              <CardsNumberInUserCollectionProvider>
                <Main />
              </CardsNumberInUserCollectionProvider>
            </TopTranformerTriggerProvider>
          </LocalForageProvider>
        </GoogleApiAuthProviderX>
      </FireBaseAuthProviderX>
    </>
  );
}
