import React, { useContext, useState } from "react";

const CardsNumberInUserCollectionContext = React.createContext();

export const useCardsNumberInUserCollection = () => {
  return useContext(CardsNumberInUserCollectionContext);
};

export const CardsNumberInUserCollectionProvider = ({ children }) => {
  const [updated, setUpdated] = useState({ updated: false });

  const update = () => setUpdated({ updated: true });

  return (
    <CardsNumberInUserCollectionContext.Provider
      value={{
        updated,
        update,
      }}
    >
      {children}
    </CardsNumberInUserCollectionContext.Provider>
  );
};
