import React, { useContext, useReducer } from "react";

const GoogleApiAuthContext = React.createContext();

export const useGoogleApiAuth = () => {
  return useContext(GoogleApiAuthContext);
};

const SIGN_IN = "singIn";
const SIGN_OUT = "signOut";

const reducer = (state, action) => {
  switch (action.type) {
    case SIGN_IN: {
      return { isSignIn: true };
    }
    case SIGN_OUT: {
      return { isSignIn: false };
    }

    default:
      return state;
  }
};

export const GoogleApiAuthProviderX = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    isSignIn: false,
  });
  
  const signIn = () => {
    dispatch({
      type: SIGN_IN,
    });
  };

  const signOut = () => {
    dispatch({
      type: SIGN_OUT,
    });
  };

  return (
    <GoogleApiAuthContext.Provider
      value={{
        isSignIn: state.isSignIn,
        signIn,
        signOut,
      }}
    >
      {children}
    </GoogleApiAuthContext.Provider>
  );
};
