import React, { useContext, useReducer } from "react";

const ShowSignInBarContext = React.createContext();

export const useShowSignInBar = () => {
  return useContext(ShowSignInBarContext);
};

const SHOW = "SHOW";
const HIDE = "HIDE";

const reducer = (state, action) => {
  switch (action.type) {
    case SHOW:
      return { ...state, status: true };
    case HIDE:
      return { ...state, status: false };
    default:
      return state;
  }
};

export const ShowSignInBarProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, { status: false });

  const show = () => dispatch({ type: SHOW });
  const hide = () => dispatch({ type: HIDE });

  return (
    <ShowSignInBarContext.Provider
      value={{
        status: state.status,
        show,
        hide,
      }}
    >
      {children}
    </ShowSignInBarContext.Provider>
  );
};
