import React, { useContext } from "react";
import localForage from "localforage";

const LocalForageContext = React.createContext();

export const useLocalForage = () => {
  return useContext(LocalForageContext);
};

export function LocalForageProvider({ children }) {
  
  const userCollection = localForage.createInstance([]);
  const colectionChunkHeight = localForage.createInstance({});
  const bookmarks = localForage.createInstance({});
  const settings = localForage.createInstance({});

  userCollection.config({
    name: "collections",
    storeName: "user_collection",
  });

  colectionChunkHeight.config({
    name: "collections",
    storeName: "colection_chunk_height",
  });

  bookmarks.config({
    name: "collections",
    storeName: "bookmarks",
  });

  settings.config({
    name: "collections",
    storeName: "settings",
  });

  

  return (
    <LocalForageContext.Provider
      value={{
        userCollection: userCollection,
        colectionChunkHeight: colectionChunkHeight,
        bookmarks: bookmarks,
        settings: settings,
      }}
    >
      {children}
    </LocalForageContext.Provider>
  );
}
