import React, { useContext, useReducer } from "react";

const TopTranformerTriggerContext = React.createContext();

export const useTopTranformerTrigger = () => {
  return useContext(TopTranformerTriggerContext);
};

const ON = "ON";
const OFF = "OFF";
const HIDE = "HIDE";
const DEFAULT = false;

const reducer = (state, action) => {
  switch (action.type) {
    case ON:
      return { ...state, status: ON };
    case OFF:
      return { ...state, status: OFF };
    case HIDE:
      return { ...state, status: HIDE };
    case DEFAULT:
      return { ...state, status: DEFAULT };
    default:
      return state;
  }
};
export const TopTranformerTriggerProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    status: false,
  });

  const on = () => dispatch({ type: ON });
  const off = () => dispatch({ type: OFF });
  const hide = () => dispatch({ type: HIDE });
  const reset = () => dispatch({ type: DEFAULT });
 
  return (
    <TopTranformerTriggerContext.Provider
      value={{
        status: state.status,
        on,
        off,
        hide,
        reset,
      }}
    >
      {children}
    </TopTranformerTriggerContext.Provider>
  );
};
